import {Vec2} from '../math';
import {Renderer} from '../renderer';
import {World} from '../world';

import {Tool} from './tool';

/**
 * Tool for removing bodies to the world.
 */
export class BodyRemover extends Tool {
  /** World to add bodies to. */
  private world: World;

  /** Renderer where the bodies are being drawn. */
  private renderer: Renderer;

  /**
   * @param world The world to remove bodies from.
   * @param renderer The renderer where the bodies are being drawn.
   */
  constructor(world: World, renderer: Renderer) {
    super();
    this.world = world;
    this.renderer = renderer;
  }

  public override activate(): void {
    // Do nothing.
  }

  public override draw(): void {
    // Do nothing.
  }

  public override onMouseDown(_: Vec2): void {
    // Do nothing.
  }

  public override onMouseUp(position: Vec2): void {
    this.world.removeBody(this.renderer.screenToWorld(position));
  }

  public override onMouseMove(_: Vec2): void {
    // Do nothing.
  }
}
