import {Body} from '../body';
import {Vec2} from '../math';
import {Renderer} from '../renderer';
import * as UI from '../ui';
import {World} from '../world';

import {Tool} from './tool';

/** Velocity multiplier for throwing bodies. */
const VELOCITY_MULTIPLIER = 0.001;

/**
 * Tool for adding bodies to the world.
 */
export class BodyAdder extends Tool {
  /** World to add bodies to. */
  private world: World;

  /** Renderer used to draw the temporary body. */
  private renderer: Renderer;

  /** Slider which indicates the body's mass. */
  private mass: UI.Slider;

  /** Body being added. */
  private body: Body;

  /** Mouse down position. */
  private mouseDown: Vec2|null;

  /**
   * @param world The world to add bodies to.
   * @param renderer The renderer used to draw the temporary body.
   * @param mass The slider which indicates the body's mass.
   */
  constructor(world: World, renderer: Renderer, mass: UI.Slider) {
    super();
    this.world = world;
    this.renderer = renderer;
    this.mass = mass;
  }

  public override activate(): void {
    this.body = new Body();
    this.mouseDown = null;
  }

  public override draw(): void {
    this.body.mass = this.mass.value;
    this.renderer.drawCircle(this.body.position, this.body.radius, this.body.color);
  }

  public override onMouseDown(position: Vec2): void {
    this.body.position = this.renderer.screenToWorld(position);
    this.mouseDown = position;
  }

  public override onMouseUp(position: Vec2): void {
    if (this.mouseDown) {
      const delta = position.sub(this.mouseDown);
      this.body.velocity = delta.mul(VELOCITY_MULTIPLIER);
      this.world.addBody(this.body);
    }

    this.activate();
  }

  public override onMouseMove(position: Vec2): void {
    if (!this.mouseDown) this.body.position = this.renderer.screenToWorld(position);
  }
}
